/* Set the background color or image for the entire app */
body {
  background-color: #97c6cc; /* Light gray background */
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.app-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: #ffffff;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
  border-radius: 10px;
}

/* Style for the upload section */
input[type="file"] {
  margin: 20px;
  padding: 10px;
  background-color: #4bbfa2;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

input[type="file"]:hover {
  background-color: #237561;
}

/* Image preview styling */
img {
  margin-top: 20px;
  width: 300px;
  height: auto;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

/* Button styling */
button {
  background-color: #bf4b6a;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  margin-top: 20px;
}

button:hover {
  background-color: #80263e;
}
